  <section id="Mon ressentit">
    <div class="bordure-texte">
      <h2>Mon ressentit</h2>
      <p>    
Visuellement, ce jeu est une vraie réussite, il est très beau et tourne parfaitement sur la Nintendo Switch 2.<br/>
Le jeu propose 32 circuits disponibles au le lancement, jouables en solo ou à plusieurs, en local comme en ligne.<br/>
Le gameplay est très fun, et l’ajout de nouvelles mécaniques comme le Charge Jump ou la fonction Rewind apporte un vent de fraîcheur à la saga.<br/>
La météo dynamique et le cycle jour/nuit, qui influencent les courses, ajoutent une touche d’immersion sympa.<br/>
Avec plus de 200 morceaux de musique tous bien composés, l'ambiance sonore est au rendez-vous.
Le mode Grand Prix est bien fourni, avec 8 coupes disponibles en 50cc, 100cc, 150cc ainsi qu’en mode miroir.<br/>
On retrouve aussi un mode Survie, structuré de la même manière avec ses propres règle.<br/>
Le mode Balade permet quant à lui d’explorer un open world, à la recherche de collectibles comme des P-Switch, des médailles Peach, ou encore des panneaux.<br/>
D'autres modes comme le Contre-la-montre ou le Mode Combat offrent une expérience alternative chouette.<br/>
Au total, 50 personnages jouables sont disponibles, accompagnés de 103 skins (sans compter les skins de base), ainsi que 40 véhicules au moment de la sortie.<br/>
Le 100 % demande un vrai investissement, notamment dans le mode Balade: il faut collecter 394 P-Switch, 150 panneaux, 200 médailles Peach, et obtenir 3 étoiles dans toutes les coupes du mode Grand Prix et Survie et encore battre tout les fantômes dans le mode Contre-la-montre.<br/>
Malheureusement, la récompense pour avoir tout complété reste assez décevante, puisqu’il s’agit principalement d’autocollants servant uniquement à décorer les véhicules.<br/>
      </p>
